		if(e.target.value === 'Industrial Land'){setFlats(false);setresidential(false);setVilla(false);setLand(false);setCommOffice(false);setITPark(false);setCommercialShop(false);setShowroom(false);setWarehouse(false);setCommercialLand(false);setIndustrialLand(true); setInBuilding(false);setInShed(false);setAgrLand(false);setFarmHouse(false);setStudioApartment(false);}
